import { Component, h, State, Element } from '@stencil/core';
import { ICellRendererComp, RowNode } from 'ag-grid-community';

/**
 * @category Data Tables
 * @description A header component for displaying a checkbox.
 */

@Component({
    tag: 'at-checkbox-header',
    shadow: false,
})
export class AtCheckboxHeaderComponent implements ICellRendererComp {
    @State() params;
    @State() disabled: boolean;
    @State() isChecked: boolean;
    @Element() el: HTMLAtCheckboxHeaderElement;

    init(params) {
        this.params = params;
        this.params.width = 60;
        this.setDisabled();

        const nodes = this.params.api.getRenderedNodes().filter(Boolean);
        const checkedNodes = nodes.filter((rowNode: RowNode) => {
            return typeof rowNode.data === 'boolean'
                ? rowNode.data
                : rowNode.data[this.params.column.colId];
        });
        this.isChecked = nodes.length && nodes.length === checkedNodes.length;
    }

    getGui(): HTMLElement {
        return this.el as unknown as HTMLElement;
    }

    refresh(params): boolean {
        this.params = params;
        this.setDisabled();
        return true;
    }

    setRowsValue(checked: boolean): void {
        if (checked !== undefined) {
            const nodes = this.params.api.getRenderedNodes().filter(Boolean);
            nodes.forEach((rowNode: RowNode) => {
                rowNode.setDataValue(this.params.column.colId, checked);
            });
        }
    }

    private setDisabled(): void {
        if (!this.params?.api) return;

        const renderedNodes = this.params.api.getRenderedNodes();
        const data: any[] = renderedNodes.map((node) => node.data);
        if (this.params.getDisabled !== undefined) {
            this.disabled = this.params.getDisabled(data);
        }
    }

    render() {
        return (
            <at-checkbox
                disabled={this.disabled}
                value={this.isChecked}
                onAtuiChange={(event: CustomEvent) =>
                    this.setRowsValue(event.detail)
                }
            ></at-checkbox>
        );
    }
}
